import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import { UserProvider } from './context/UserContext'
import Login from './pages/Login'
import Home from './pages/Home'
import SideBar from './components/SideBar'
import { SidebarProvider } from './context/SideBarContext'
import { useState } from 'react';
import Consulta from "./pages/Consulta";
import Paciente from "./pages/Paciente";
import Profissional from "./pages/Profissional";
import ConvenioMedico from './pages/CadastrosBasicos/ConvenioMedico'
import Profissao from './pages/CadastrosBasicos/Profissao'


function App() {
  const [token] = useState(localStorage.getItem('token') || '');

  return (
    <div className="App">
      <Router>
        <UserProvider>
          <SidebarProvider>
            {token &&
              <>
                <SideBar />
                <ConvenioMedico />
                <Profissao />
                <Paciente />
              </>}
            <main className="relative w-9 m-auto" style={{ height: '100vh' }} >
              <Routes>
                <Route exact path="/" element={<Login />} />
                <Route exact path="/home" element={<Home />} />
                <Route exact path="/consulta" element={<Consulta />} />
                <Route exact path="/profissional" element={<Profissional />} />
              </Routes>
            </main>
          </SidebarProvider>
        </UserProvider>
      </Router>
    </div>
  );
}

export default App;
