
import React, { useState, useEffect, useRef, useContext } from 'react';
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import { ConsultaService } from './service/ConsultaService.js';
import { SidebarContext } from '../../context/SideBarContext';
import { Toast } from 'primereact/toast';
import { Button } from 'primereact/button';
import { Toolbar } from 'primereact/toolbar';
import { Dialog } from 'primereact/dialog';
import { Card } from 'primereact/card';
import { Dropdown } from 'primereact/dropdown';
import { Calendar } from 'primereact/calendar';
import { Checkbox } from "primereact/checkbox";
import { InputTextarea } from 'primereact/inputtextarea';
import { FilterMatchMode, FilterOperator } from 'primereact/api';


export default function Consulta() {
    const { setPacienteVisible } = useContext(SidebarContext);

    let emptyConsulta = {
        _id: null,
        data: '',
        hora: '',
        paciente: '',
        profissional: '',
        user: '',
        atendida: false,
        status: '',
        tipo: '',
        observacoes: ''
    };

    const [consultas, setConsultas] = useState([]);
    const [pacientes, setPacientes] = useState([]);
    const [profissionais, setProfissionais] = useState([]);
    const [consultaDialog, setConsultaDialog] = useState(false);
    const [deleteConsultaDialog, setDeleteConsultaDialog] = useState(false);
    const [consulta, setConsulta] = useState(emptyConsulta);
    const [selectedConsultas, setSelectedConsultas] = useState(null);
    const [submitted, setSubmitted] = useState(false);
    const [globalFilter, setGlobalFilter] = useState(null);
    const [expandedRows, setExpandedRows] = useState([]);
    const toast = useRef(null);
    const dt = useRef(null);

    useEffect(() => {
        const currentToken = localStorage.getItem('token') || '';
        ConsultaService.getConsultas(currentToken)
            .then(response => {
                setConsultas(response.data); // Acesso  array de consultas
            })
            .catch(error => {
                console.error("Erro ao buscar consultas:", error);
            });
    }, []);

    useEffect(() => {
        const currentToken = localStorage.getItem('token') || '';
        ConsultaService.getPacientes(currentToken)
            .then(response => {
                setPacientes(response.data.pacientes); // Acesso  array de pacientes
            })
            .catch(error => {
                console.error("Erro ao buscar pacientes:", error);
            });
    }, []);

    useEffect(() => {
        const currentToken = localStorage.getItem('token') || '';
        ConsultaService.getProfissionais(currentToken)
            .then(response => {
                setProfissionais(response.data); // Acesso  array de profissionais
            })
            .catch(error => {
                console.error("Erro ao buscar profissionais:", error);
            });
    }, []);

    const formatDate = (dateStr) => {
        if (!dateStr) return '';
        const date = new Date(dateStr);
        return date instanceof Date && !isNaN(date) ? date.toLocaleDateString() : '';
    };

    const formatTime = (timeStr) => {
        if (!timeStr) return '';
        const time = new Date(timeStr);
        return time instanceof Date && !isNaN(time) ? time.toLocaleTimeString() : '';
    };

    const headerTemplate = (data) => {
        return (
            <React.Fragment>
                <span className="vertical-align-middle ml-2 font-bold line-height-3">{data?.profissional?.nome || 'Profissional No Definido'}</span>
            </React.Fragment>
        );
    };


    const openNew = () => {
        setConsulta(emptyConsulta);
        setSubmitted(false);
        setConsultaDialog(true);
    };

    const hideDialog = () => {
        setSubmitted(false);
        setConsultaDialog(false);
    };

    const hideDeleteConsultaDialog = () => {
        setDeleteConsultaDialog(false);
    };

    const saveConsulta = () => {
        setSubmitted(true);

        if (consulta.tipo.trim()) {
            let _consultas = [...consultas];
            let _consulta = { ...consulta };

            const currentToken = localStorage.getItem('token') || '';
            if (consulta._id) {
                const index = findIndexById(consulta._id);
                _consultas[index] = _consulta;
                toast.current.show({ severity: 'success', summary: 'Sucesso', detail: 'Consulta Atualizado', life: 3000 });
                ConsultaService.updateConsulta(_consulta, currentToken);
            } else {
                console.log("create consulta", _consulta);
                _consulta._id = createId();
                _consultas.push(_consulta);
                toast.current.show({ severity: 'success', summary: 'Sucesso', detail: 'Consulta Criado', life: 3000 });
                ConsultaService.createConsulta(_consulta, currentToken);
            }

            setConsultas(_consultas);
            setConsultaDialog(false);
            setConsulta(emptyConsulta);
        }
    };

    const editConsulta = (consulta) => {
        setConsulta({ ...consulta });
        setConsultaDialog(true);
    };

    const confirmDeleteConsulta = (consulta) => {
        setConsulta(consulta);
        setDeleteConsultaDialog(true);
    };

    const deleteConsulta = () => {
        const currentToken = localStorage.getItem('token') || '';

        if (consulta && consulta._id) {

            ConsultaService.deleteConsulta(consulta._id, currentToken)
                .then(() => {
                    toast.current.show({ severity: 'success', summary: 'Sucesso', detail: 'Consulta Deletado', life: 3000 });
                    setConsultas(consultas.filter(val => val._id !== consulta._id));
                    setDeleteConsultaDialog(false);
                    setConsulta(emptyConsulta);
                })
                .catch(error => {
                    console.error("Erro ao deletar consulta:", error);
                });
        } else {
            console.error("Erro: id do consulta  undefined");
        }
    };


    const exportCSV = (selectionOnly) => {
        dt.current.exportCSV({ selectionOnly });
    };

    const findIndexById = (_id) => {
        let index = -1;
        for (let i = 0; i < consultas.length; i++) {
            if (consultas[i]._id === _id) {
                index = i;
                break;
            }
        }
        return index;
    };

    const createId = () => {
        return Math.random().toString(36).substr(2, 9);
    };

    const onInputChange = (e, name) => {
        const val = (e.target && e.target.value) || '';
        let _consulta = { ...consulta };
        _consulta[`${name}`] = val;
        setConsulta(_consulta);
    };

    const leftToolbarTemplate = () => {
        return (
            <React.Fragment>
                <Button label="Novo" icon="pi pi-plus" className="border-round p-button-success mr-2" onClick={openNew} />
                {/* <Button label="Deletar" icon="pi pi-trash" className="border-round p-button-danger" onClick={() => confirmDeleteConsulta(selectedConsultas)} disabled={!selectedConsultas || !selectedConsultas.length} /> */}
            </React.Fragment>
        );
    };

    const actionBodyTemplate = (rowData) => {
        return (
            <React.Fragment>
                <Button icon="pi pi-pencil" className="border-round p-button-rounded p-button-success mr-2" onClick={() => editConsulta(rowData)} />
                <Button icon="pi pi-trash" className="border-round p-button-rounded p-button-danger" onClick={() => confirmDeleteConsulta(rowData)} />
            </React.Fragment>
        );
    };

    const expandAll = () => {
        let _expandedRows = [];
        consultas.forEach((consulta) => {
            // console.log("consulta", consulta);
            // (_expandedRows[`${consulta._id}`] = true)
            _expandedRows.push(consulta._id);
        });
        console.log(_expandedRows);
        setExpandedRows(_expandedRows);
    };

    const collapseAll = () => {
        setExpandedRows([]);
    };

    const [filters, setFilters] = useState({
        global: { value: null, matchMode: FilterMatchMode.CONTAINS },
    });

    const onGlobalFilterChange = (event) => {
        const value = event.target.value;
        let _filters = { ...filters };

        _filters['global'].value = value;

        setFilters(_filters);
    };

    const renderHeader = () => {
        const value = filters['global'] ? filters['global'].value : '';

        return (
            <div className="table-header flex justify-between align-items-center">
                <h5 className="mx-0 my-1">Gerenciar Consultas</h5>
                {/* <span className="p-input-icon-left">
                    <i className="pi pi-search" />
                    <InputText type="search" value={value || ''} onChange={(e) => onGlobalFilterChange(e)} placeholder="Global Search" />
                </span> */}

                <Button type="button" className='ml-5 border-round' icon="pi pi-file-excel" severity="success" onClick={() => exportCSV(false)} data-pr-tooltip="CSV" />

                {/* <Button icon="pi pi-plus" label="Expand All" onClick={expandAll} text />
            <Button icon="pi pi-minus" label="Collapse All" onClick={collapseAll} text /> */}
            </div>
        )
    }

    const header = renderHeader();

    const consultaDialogFooter = (
        <React.Fragment>
            <Button label="Cancelar" icon="pi pi-times" className="border-round p-button-text" onClick={hideDialog} />
            <Button label="Salvar" icon="pi pi-check" className="border-round p-button-text" onClick={saveConsulta} />
        </React.Fragment>
    );

    const deleteConsultaDialogFooter = (
        <React.Fragment>
            <Button label="No" icon="pi pi-times" className="border-round p-button-text" onClick={hideDeleteConsultaDialog} />
            <Button label="Sim" icon="pi pi-check" className="border-round p-button-text" onClick={deleteConsulta} />
        </React.Fragment>
    );

    return (
        <div>
            <Toast ref={toast} />

            <Card>
                <span className="p-text-center p-mb-4" style={{ fontSize: '24px', color: '#333', borderBottom: 'solid 1px #6c757d' }}>
                    Cadastro de Consulta
                </span>
            </Card>

            <div className="card">
                <Toolbar className="mb-4" left={leftToolbarTemplate}></Toolbar>

                <DataTable ref={dt} value={consultas} selection={selectedConsultas} onSelectionChange={e => setSelectedConsultas(e.value)}
                    dataKey="_id" paginator rows={10} rowsPerPageOptions={[5, 10, 25]} rowGroupMode="subheader" groupRowsBy="profissional.nome" sortOrder={1}
                    sortField="profissional.nome" paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink CurrentPageReport RowsPerPageDropdown"
                    expandableRowGroups expandedRows={expandedRows} onRowToggle={(e) => setExpandedRows(e.data)} sortMode="single" rowGroupHeaderTemplate={headerTemplate}
                    currentPageReportTemplate="Mostrando {first} a {last} de {totalRecords} consultas" globalFilter={globalFilter} header={header}>
                    <Column field="profissional.nome" header="Profissional" sortable></Column>
                    <Column field="paciente.nome" header="Paciente" sortable></Column>
                    <Column field="data" header="Data" body={(rowData) => formatDate(rowData.data)} sortable></Column>
                    <Column field="hora" header="Hora" body={(rowData) => formatTime(rowData.hora)} sortable></Column>
                    <Column field="status" header="Status" sortable></Column>
                    <Column field="tipo" header="Tipo" sortable></Column>
                    <Column body={actionBodyTemplate}></Column>
                </DataTable>
            </div>

            <Dialog visible={consultaDialog} style={{ width: '850px', margin: 'auto' }} header="Detalhes do Consulta" modal className="p-fluid" footer={consultaDialogFooter} onHide={hideDialog}>

                <div className='grid'>
                    <div className="field col-6">
                        <label htmlFor="profissional">Profissional</label>
                        <Dropdown editable  id='profissional' value={consulta.profissional} onChange={(e) => onInputChange(e, 'profissional')} options={profissionais} optionLabel="nome"
                            placeholder="Selecione o Profissional" className="w-full" />
                    </div>
                    <div className="field col-6">
                        <label htmlFor="email">Paciente</label>
                        <div className="p-inputgroup flex-1">
                            <Dropdown editable  id='paciente' value={consulta.paciente} onChange={(e) => onInputChange(e, 'paciente')} options={pacientes} optionLabel="nome"
                                placeholder="Selecione o Paciente" className="w-full" />
                            <Button icon="pi pi-plus" onClick={() => setPacienteVisible(true)} className="p-button-success border-round-right" />
                        </div>
                    </div>

                    <div className="field col-6">
                        <label htmlFor="status">Status</label>
                        <Dropdown editable  value={consulta.status} onChange={(e) => onInputChange(e, 'status')} options={["Agendada", "Cancelada", "Realizada"]}
                            placeholder="Selecione o Status" className="w-full" />
                    </div>
                    <div className="field col-6">
                        <label htmlFor="tipo">Tipo</label>
                        <Dropdown editable  value={consulta.tipo} onChange={(e) => onInputChange(e, 'tipo')} options={["Consulta", "Retorno"]}
                            placeholder="Selecione o Tipo" className="w-full" />
                    </div>
                </div>
                <div className='grid'>
                    <div className="field col">
                        <label htmlFor="data">Data</label>
                        <Calendar _id="data" value={consulta.data} onChange={(e) => onInputChange(e, 'data')} showIcon />
                    </div>

                    <div className="field col">
                        <label htmlFor="hora">Hora</label>
                        <Calendar _id="hora" value={consulta.hora} onChange={(e) => onInputChange(e, 'hora')} timeOnly />
                    </div>
                </div>
                <div className='grid'>
                    <div className="field col">
                        <label htmlFor="observacoes">Observaes</label>
                        <InputTextarea _id="observacoes" value={consulta.observacoes} onChange={(e) => onInputChange(e, 'observacoes')} />
                    </div>
                </div>
                <div className="flex align-items-center">
                    <Checkbox inputId="atendida" value={consulta.atendida} onChange={(e) => onInputChange(e, 'atendida')} />
                    <label className="ml-2" htmlFor="atendida">Atendida</label>
                </div>
            </Dialog>

            <Dialog visible={deleteConsultaDialog} style={{ width: '450px' }} header="Confirmao" modal footer={deleteConsultaDialogFooter} onHide={hideDeleteConsultaDialog}>
                <div className="confirmation-content">
                    <i className="pi pi-exclamation-triangle mr-2" style={{ fontSize: '2rem' }} />
                    {consulta && <span>Tem certeza que deseja excluir o consulta <b>{consulta.nome}</b>?</span>}
                </div>
            </Dialog>
        </div>
    );
}
