// ConvenioService.js
import api from '../../../../utils/api';

export class ConvenioService {
    static getConvenios(token) {
        return api.get('/convenios/getAll', {
            headers: {
                Authorization: `Bearer ${JSON.parse(token)}`
            }
        });
    }

    static createConvenio(convenio, token) {
        return api.post('/convenios/create', convenio, {
            headers: {
                Authorization: `Bearer ${JSON.parse(token)}`
            }
        });
    }

    static updateConvenio(convenio, token) {
        console.log("update ",convenio);
        return api.put(`/convenios/update/${convenio._id}`, convenio, {
            headers: {
                Authorization: `Bearer ${JSON.parse(token)}`
            }
        });
    }

    static deleteConvenio(id, token) {
        console.log("delete ",id);
        return api.delete(`/convenios/delete/${id}`, {
            headers: {
                Authorization: `Bearer ${JSON.parse(token)}`
            }
        });
    }
}
